.gastronomybox{display:flex;flex-direction:column;justify-content:space-between;margin:40px;margin-bottom:0;flex:1;max-width:calc(100% - 161px)}.gastronomybox__title{width:fit-content;font-family:"Geniral";font-style:normal;font-size:380px;font-weight:400;line-height:90%;letter-spacing:.01em;text-transform:uppercase;color:#101010;white-space:nowrap;position:relative;animation:scroll-left 25s infinite linear}.gastronomybox__title::after{position:absolute;content:"Гастрономия Гастрономия "}.gastronomybox__title-inner{overflow:hidden;max-width:960px;width:100%}.gastronomybox__title-wrapper{width:100%;display:flex;justify-content:space-between;flex:1}.gastronomybox__grid{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(3, 1fr);grid-column-gap:40px}.gastronomybox__next{display:flex;align-items:center;height:40px;gap:19px;white-space:nowrap;overflow:hidden;border-top:1px solid #bebebe;margin-left:-40px;margin-right:-40px;padding-left:10px}.gastronomybox__next span{white-space:nowrap;font-family:"Akrobat";font-style:normal;font-weight:500;font-size:12px;line-height:14px;letter-spacing:.01em;text-transform:uppercase;color:#101010}@keyframes scroll-left{0%{transform:translateX(0)}100%{transform:translateX(-100%)}}